"use client";

import { useState, FormEvent, useEffect } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { useAuthStore } from "@/store/authStore";
import Button from "@/components/ui/Button";
import Input from "@/components/ui/Input";
import Card from "@/components/ui/Card";
import { Sparkles, Mail, ArrowLeft, CheckCircle } from "lucide-react";
import Link from "next/link";

export default function VerifyEmailPage() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const email = searchParams.get("email") || "";
  
  const { verifyEmail, isLoading, error, clearError } = useAuthStore();
  const [code, setCode] = useState("");
  const [localError, setLocalError] = useState<string | null>(null);
  const [success, setSuccess] = useState(false);
  const [resendSuccess, setResendSuccess] = useState(false);

  useEffect(() => {
    if (!email) {
      router.push("/register");
      return;
    }
    clearError();
  }, [email, router, clearError]);

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    clearError();
    setLocalError(null);
    setResendSuccess(false);

    // Validation
    if (!code) {
      setLocalError("Doƒürulama kodu gereklidir");
      return;
    }

    if (code.length !== 6) {
      setLocalError("Doƒürulama kodu 6 haneli olmalƒ±dƒ±r");
      return;
    }

    // Backend'e doƒürulama isteƒüi g√∂nder
    const success = await verifyEmail(email, code);
    
    if (success) {
      console.log("‚úÖ Email doƒürulama ba≈üarƒ±lƒ±!");
      setSuccess(true);
      
      // 2 saniye sonra login sayfasƒ±na y√∂nlendir
      setTimeout(() => {
        router.push("/login");
      }, 2000);
    } else {
      // Hata zaten store'da set edildi, displayError'da g√∂sterilecek
    }
  };

  const handleResend = () => {
    setLocalError(null);
    setResendSuccess(false);

    // Frontend'de yeni kod olu≈ütur
    const verificationCode = Math.floor(100000 + Math.random() * 900000).toString();
    
    const verificationData = {
      email: email,
      code: verificationCode,
      expiresAt: Date.now() + 5 * 60 * 1000, // 5 dakika
    };
    localStorage.setItem("email-verification", JSON.stringify(verificationData));
    
    console.log("‚úÖ Yeni doƒürulama kodu olu≈üturuldu.");
    console.log("üîë Yeni Doƒürulama Kodu (TEST):", verificationCode);
    console.log("üí° Bu kod sadece test i√ßin g√∂steriliyor. Production'da email ile g√∂nderilecek.");
    
    setResendSuccess(true);
    setTimeout(() => {
      setResendSuccess(false);
    }, 5000);
  };

  const displayError = localError || error;

  if (success) {
    return (
      <div className="min-h-screen bg-dark-950 flex items-center justify-center p-4">
        <div className="w-full max-w-md">
          <Card className="p-8 text-center">
            <div className="inline-flex items-center justify-center w-16 h-16 bg-green-600/20 rounded-full mb-4">
              <CheckCircle className="w-8 h-8 text-green-400" />
            </div>
            <h2 className="text-2xl font-bold text-foreground mb-2">Email Doƒürulandƒ±!</h2>
            <p className="text-muted-foreground mb-6">
              Hesabƒ±nƒ±z ba≈üarƒ±yla aktifle≈ütirildi. Giri≈ü sayfasƒ±na y√∂nlendiriliyorsunuz...
            </p>
            <Button
              onClick={() => router.push("/login")}
              className="w-full"
            >
              Giri≈ü Sayfasƒ±na Git
            </Button>
          </Card>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-dark-950 flex items-center justify-center p-4">
      <div className="w-full max-w-md">
        {/* Logo & Title */}
        <div className="text-center mb-8">
          <div className="inline-flex items-center justify-center w-16 h-16 bg-gradient-to-br from-primary-500 to-primary-700 rounded-2xl mb-4">
            <Sparkles className="w-8 h-8 text-white" />
          </div>
          <h1 className="text-3xl font-bold text-foreground mb-2">Email Doƒürulama</h1>
          <p className="text-muted-foreground">
            <strong>{email}</strong> adresine g√∂nderilen doƒürulama kodunu giriniz
          </p>
        </div>

        {/* Verify Card */}
        <Card className="p-8">
          <form onSubmit={handleSubmit} className="space-y-6">
            {/* Error Message */}
            {displayError && (
              <div className="p-4 bg-red-600/10 border border-red-600/30 rounded-lg">
                <p className="text-red-400 text-sm">{displayError}</p>
              </div>
            )}

            {/* Success Message */}
            {resendSuccess && (
              <div className="p-4 bg-green-600/10 border border-green-600/30 rounded-lg">
                <p className="text-green-400 text-sm">Doƒürulama kodu tekrar g√∂nderildi!</p>
              </div>
            )}

            {/* Code Field */}
            <div className="space-y-2">
              <label htmlFor="code" className="block text-sm font-medium text-foreground">
                Doƒürulama Kodu <span className="text-red-400">*</span>
              </label>
              <div className="relative">
                <Mail className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-muted-foreground" />
                <Input
                  id="code"
                  type="text"
                  value={code}
                  onChange={(e) => {
                    // Sadece rakamlarƒ± kabul et ve maksimum 6 karakter
                    const value = e.target.value.replace(/\D/g, "").slice(0, 6);
                    setCode(value);
                  }}
                  placeholder="123456"
                  className="pl-10 text-center text-2xl tracking-widest"
                  disabled={isLoading}
                  required
                  maxLength={6}
                  autoComplete="one-time-code"
                />
              </div>
              <p className="text-xs text-muted-foreground">
                6 haneli doƒürulama kodunu giriniz (Email adresinize g√∂nderilen kodu giriniz)
              </p>
            </div>

            {/* Submit Button */}
            <Button
              type="submit"
              className="w-full"
              disabled={isLoading || code.length !== 6}
            >
              {isLoading ? "Doƒürulanƒ±yor..." : "Doƒürula"}
            </Button>

            {/* Resend Code */}
            <div className="text-center">
              <button
                type="button"
                onClick={handleResend}
                disabled={isLoading}
                className="text-sm text-primary-400 hover:text-primary-300 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                Kodu tekrar g√∂nder
              </button>
            </div>

            {/* Back to Register */}
            <div className="pt-4 border-t border-dark-800">
              <Link
                href="/register"
                className="flex items-center justify-center gap-2 text-sm text-muted-foreground hover:text-foreground transition-colors"
              >
                <ArrowLeft className="w-4 h-4" />
                Kayƒ±t sayfasƒ±na d√∂n
              </Link>
            </div>
          </form>
        </Card>
      </div>
    </div>
  );
}

