import { create } from "zustand";
import { persist } from "zustand/middleware";
import { authApi } from "@/lib/api";

export interface User {
  id: string;
  email: string;
  name?: string;
}

interface AuthStore {
  user: User | null;
  token: string | null;
  isLoading: boolean;
  error: string | null;
  
  // Actions
  login: (email: string, password: string) => Promise<boolean>;
  register: (email: string, password: string, name?: string) => Promise<boolean>;
  logout: () => void;
  forgotPassword: (email: string) => Promise<boolean>;
  resetPassword: (token: string, newPassword: string) => Promise<boolean>;
  verifyEmail: (email: string, code: string) => Promise<boolean>;
  resendVerificationCode: (email: string) => Promise<boolean>;
  setError: (error: string | null) => void;
  clearError: () => void;
}

export const useAuthStore = create<AuthStore>()(
  persist(
    (set) => ({
      user: null,
      token: null,
      isLoading: false,
      error: null,

      login: async (email: string, password: string) => {
        set({ isLoading: true, error: null });
        const response = await authApi.login(email, password);
        
        if (response.error) {
          set({ 
            error: response.error.message, 
            isLoading: false,
            user: null,
            token: null,
          });
          return false;
        }

        if (response.data) {
          set({
            user: response.data.user,
            token: response.data.token,
            isLoading: false,
            error: null,
          });
          return true;
        }
        
        set({ isLoading: false });
        return false;
      },

      register: async (email: string, password: string, name?: string) => {
        console.log("üîµ authStore.register √ßaƒürƒ±ldƒ±");
        set({ isLoading: true, error: null });
        
        console.log("üì§ authApi.register √ßaƒürƒ±lƒ±yor...");
        const response = await authApi.register(email, password, name);
        
        console.log("üì• authApi.register response:", response);
        console.log("üì• response.error:", response.error);
        console.log("üì• response.data:", response.data);
        
        if (response.error) {
          console.error("‚ùå Register Error:", response.error);
          
          // √ñzel durum: Kullanƒ±cƒ± olu≈üturuldu ama email g√∂nderilemedi
          // Bu durumda kullanƒ±cƒ±yƒ± email doƒürulama sayfasƒ±na y√∂nlendirebiliriz
          const errorMessage = response.error.message || "";
          if (errorMessage.toLowerCase().includes("user created") || 
              errorMessage.toLowerCase().includes("kullanƒ±cƒ± olu≈üturuldu")) {
            console.log("‚ÑπÔ∏è Kullanƒ±cƒ± olu≈üturuldu ama email g√∂nderilemedi, email doƒürulama sayfasƒ±na y√∂nlendirilecek");
            set({
              isLoading: false,
              error: null, // Hata mesajƒ±nƒ± g√∂sterme, direkt y√∂nlendir
            });
            return true; // Ba≈üarƒ±lƒ± say, y√∂nlendirme yapƒ±lsƒ±n
          }
          
          set({ 
            error: response.error.message, 
            isLoading: false,
            user: null,
            token: null,
          });
          console.log("‚ùå Register false d√∂nd√ºr√ºyor (error var)");
          return false;
        }

        // Backend ba≈üarƒ±lƒ± response d√∂nd√ºrd√ºyse (error yoksa) ba≈üarƒ±lƒ± say
        // Backend muhtemelen { message: "..." } veya { user: {...} } formatƒ±nda d√∂nd√ºr√ºyor
        console.log("‚úÖ Register ba≈üarƒ±lƒ±! (error yok)");
        console.log("‚úÖ Register response:", response);
        console.log("‚úÖ Register response.data:", response.data);
        
        // Register ba≈üarƒ±lƒ±, email doƒürulama sayfasƒ±na y√∂nlendirilecek
        // User bilgilerini kaydetme, sadece ba≈üarƒ±lƒ± olduƒüunu belirt
        set({
          isLoading: false,
          error: null,
        });
        
        console.log("‚úÖ Register true d√∂nd√ºr√ºyor");
        return true;
      },

      logout: () => {
        set({
          user: null,
          token: null,
          error: null,
        });
      },

      forgotPassword: async (email: string) => {
        set({ isLoading: true, error: null });
        const response = await authApi.forgotPassword(email);
        
        if (response.error) {
          set({ 
            error: response.error.message, 
            isLoading: false,
          });
          return false;
        }

        set({ isLoading: false, error: null });
        return true;
      },

      resetPassword: async (token: string, newPassword: string) => {
        set({ isLoading: true, error: null });
        const response = await authApi.resetPassword(token, newPassword);
        
        if (response.error) {
          set({ 
            error: response.error.message, 
            isLoading: false,
          });
          return false;
        }

        set({ isLoading: false, error: null });
        return true;
      },

      verifyEmail: async (email: string, code: string) => {
        set({ isLoading: true, error: null });
        const response = await authApi.verifyEmail(email, code);
        
        if (response.error) {
          set({ 
            error: response.error.message, 
            isLoading: false,
          });
          return false;
        }

        set({ isLoading: false, error: null });
        return true;
      },

      resendVerificationCode: async (email: string) => {
        set({ isLoading: true, error: null });
        const response = await authApi.resendVerificationCode(email);
        
        if (response.error) {
          set({ 
            error: response.error.message, 
            isLoading: false,
          });
          return false;
        }

        set({ isLoading: false, error: null });
        return true;
      },

      setError: (error: string | null) => set({ error }),
      clearError: () => set({ error: null }),
    }),
    {
      name: "auth-storage",
      partialize: (state: AuthStore) => ({ 
        user: state.user, 
        token: state.token 
      }),
    }
  )
);

